.example {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
        Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1.5;
    background-color: #fff;
    color: #424242;
    max-width: 960px;
    padding: 16px;
}

li {
    list-style: none;
}
.search-form {
    z-index: 2;

    display: inline-block;
    position: fixed;

    left: 50%;
    transform: translate(-50%, -35%);

    border: 2px solid black;
    border-radius: 5px;
    background-color: black;
}
.button {
    position: sticky;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: inline-flex;
    align-items: center;
    align-self: center;

    font-weight: 900;
    padding: 10px 30px;

    background-color: blue;
    color: #fff;
    border-radius: 5px;
}
.gallery {
    display: flex;
    flex-wrap: wrap;
    padding-top: 30px;
    padding-left: 0;
    margin: 20px;
}
.stats {
    display: flex;
    justify-content: center;
}
.stats .stats-item:not(:last-child) {
    margin-right: 10px;
}
.button {
    opacity: 0;
}
.button.is-active {
    opacity: 1;
}
.gallery__item {
    margin: 20px;
    border-radius: 25px;
    overflow: hidden;
}
.photo-card {
    position: relative;
}
.stats {
    position: absolute;
    bottom: 2px;
    left: 0;
    width: -webkit-fill-available;
    background-color: rgba(241, 233, 231, 0.4);

    border-radius: 5px;
}
